  width: 19px;
  height: 19px;
  background: url(../img/acc-arrow.svg) center/cover no-repeat;
  transform: rotate(-90deg);
  transition: transform 0.2s ease;
}

@media (max-width: 1215px) {
  .accordion-triger::after {
    top: 50%;
    transform: translateY(-50%) rotate(-90deg);
  }
}

@media (max-width: 991.98px) {
  .accordion-triger {
    padding-right: 0;
  }
  .accordion-triger::after {
    display: none;
  }
}

.accordion--active .accordion-triger::after {
  transform: rotate(0deg);
  transition: transform 0.2s ease;
}

@media (max-width: 1215px) {
  .accordion--active .accordion-triger::after {
    transform: translateY(-50%) rotate(0deg);
  }
}

/*! Consultation */
.consultation {
  padding-top: calc(250px / 2);
  padding-bottom: calc(180px / 2);
}

@media (max-width: 767.98px) {
  .consultation {
    padding-top: calc(190px / 2);
    padding-bottom: calc(150px / 2);
  }
}

@media (max-width: 479.98px) {
  .consultation {
    padding-top: calc(120px / 2);
    padding-bottom: calc(102px / 2);
  }
}

.consultation__container {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 560px) {
  .consultation__container {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.consultation__form {
  flex: 0 0 33.33%;
  display: flex;
  flex-direction: column;
}

@media (max-width: 991.98px) {
  .consultation__form {
    flex: 0 0 50%;
  }
}

@media (max-width: 560px) {
  .consultation__form {
    flex: auto;
    margin-bottom: 71px;
  }
}

.consultation__nav {
  flex: 0 0 33.33%;
}

@media (max-width: 991.98px) {
  .consultation__nav {
    flex: 0 0 25%;
  }
}

@media (max-width: 560px) {
  .consultation__nav {
    flex: auto;
  }
}

.consultation__item:not(:last-child) {
  margin-bottom: 25px;
}

@media (max-width: 767.98px) {
  .consultation__item {
    font-size: 16px;
  }
  .consultation__item:not(:last-child) {
    margin-bottom: 22px;
  }
}

.consultation__link {
  position: relative;
  display: inline-block;
  padding: 1px 0;
  text-decoration: underline;
  transition: transform 0.2s ease;
}

@media (min-width: 767.98px) {
  .consultation__link:hover {
    transform: scale(1.1);
    transition: transform 0.2s ease;
  }
  .consultation__link:focus {
    transform: scale(1.1);
    transition: transform 0.2s ease;
  }
}

.form *:not(:last-child) {
  margin-bottom: 20px;
}

@media (max-width: 767.98px) {
  .form *:not(:last-child) {
    margin-bottom: 12px;
  }
}

.form__heading:not(:last-child) {
  margin-bottom: 40px;
}

@media (max-width: 767.98px) {
  .form__heading:not(:last-child) {
    margin-bottom: 24px;
  }
}

.form__input {
  padding: 19px 20px 17px 20px;
  border: 1px solid #787878;
  background-color: transparent;
  color: #787878;
  font-size: 17px;
  line-height: 24px;
  resize: none;
  transition: border 0.3s ease, color 0.3s ease;
  outline: none;
}

.form__input::placeholder {
  color: #787878;
  transition: color 0.3s ease;
}

.form__input--invalid {
  border: 1px solid #f2994a;
  color: #f2994a;
}

.form__input--invalid::placeholder {
  color: #f2994a;
}

.form__input--textarea {
  height: 120px;
  margin-bottom: 22px;
}

@media (max-width: 767.98px) {
  .form__input--textarea {
    height: 144px;
  }
}

@media (max-width: 767.98px) {
  .form__input {
    padding: 19px 20px 17px 25px;
    font-size: 14px;
  }
}

@media (min-width: 767.98px) {
  .form__input:hover {
    border: 1px solid #262626;
    color: #262626;
    transition: border 0.3s ease, color 0.3s ease;
  }
  .form__input:hover::placeholder {
    color: #262626;
  }
  .form__input:focus {
    border: 1px solid #262626;
    color: #262626;
    transition: border 0.3s ease, color 0.3s ease;
  }
  .form__input:focus::placeholder {
    color: #262626;
  }
}

.form__input:focus:not(:focus-visible) {
  outline: none;
}

@media (max-width: 767.98px) {
  .form__input {
    outline: none;
  }
}

.form__checkbox-label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: rgba(38, 38, 38, 0.5);
  font-size: 14px;
  line-height: 150%;
  text-decoration: underline;
}

@media (max-width: 767.98px) {
  .form__checkbox-label {
    align-items: flex-start;
    font-size: 12px;
    margin-bottom: 50px !important;
  }
}

.form__checkbox {
  position: relative;
  margin-right: 14px;
}

.form__checkbox::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  transform: translate(-50%, -50%);
}

.form__checkbox::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: #e9ddd7;
  border: 1px solid #787878;
  transform: translate(-50%, -50%);
}

.form__checkbox:checked::after {
  background-color: #f2994a;
}

.form__checkbox:checked::before {
  width: 10px;
  height: 6px;
  border: none;
  background: url(../img/done-form.svg) center/cover no-repeat;
}

@media (min-width: 767.98px) {
  .form__checkbox:hover::before {
    background-color: #787878;
  }
  .form__checkbox:hover:checked::after {
    background-color: #f2994a;
  }
  .form__checkbox:hover:checked::before {
    width: 10px;
    height: 6px;
    border: none;
    background: url(../img/done-form.svg) center/cover no-repeat;
    background-color: #f2994a;
  }
}

.form__sibmit {
  align-self: flex-start;
}

@media (max-width: 560px) {
  .form__sibmit {
    align-self: center;
  }
}

/*! Footer */
.footer__container {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 991.98px) {
  .footer__container {
    flex-wrap: wrap;
  }
}

@media (max-width: 600px) {
  .footer__container {
    flex-direction: column;
  }
}

.footer__item {
  flex: 0 0 33.33%;
  margin-bottom: 10px;
}

@media (max-width: 991.98px) {
  .footer__item {
    flex: 0 0 50%;
  }
  .footer__item:nth-child(3n + 1) {
    flex: 0 0 100%;
  }
}

@media (max-width: 600px) {
  .footer__item {
    flex: auto;
  }
}

.footer__link {
  color: #787878;
  font-size: 14px;
  line-height: 200%;
  text-decoration-line: underline;
}

@media (max-width: 767.98px) {
  .footer__link {
    font-size: 13px;
  }
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.7s ease, visibility 0.7s ease;
}

.modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../img/bg.png) 0/250px repeat;
  background-color: #e9dcd7;
}

.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal.open .modal__content {
  opacity: 1;
  transform: translate(0px, 0px);
}

.modal__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1230px;
  width: 100%;
  padding: 0 15px;
  opacity: 0;
  color: black;
  transform: translate(0px, -100%);
  transition: opacity 0.7s ease, transform 0.7s ease;
}

.modal__close {
  position: absolute;
  top: 0;
  right: 30px;
  z-index: 1;
  display: block;
  background: transparent;
  color: black;
  font-size: 40px;
  font-weight: 300;
  cursor: pointer;
  transition: transform 0.2s ease-in;
}

.modal__close:focus:not(:focus-visible) {
  outline: none;
}

@media (max-width: 767.98px) {
  .modal__close {
    outline: none;
  }
}

@media (min-width: 767.98px) {
  .modal__close:hover {
    transform: scale(1.2);
  }
  .modal__close:focus {
    transform: scale(1.2);
  }
}

.modal__body {
  position: relative;
}

.modal__video {
  width: 100%;
}

.modal__video:focus:not(:focus-visible) {
  outline: none;
}

@media (max-width: 767.98px) {
  .modal__video {
    outline: none;
  }
}

.modal__btn {
  position: absolute;
  bottom: 50px;
  right: 50px;
  width: 150px;
  height: 150px;
  background: url(../img/slide-arrow.svg) center/cover no-repeat;
}

@media (max-width: 991.98px) {
  .modal__btn {
    display: none;
  }
}

